<template>
  <div id="app">
    <div id="app-container" class="w-3/4 flex mx-auto rounded-lg shadow-lg">
      <div class="w-3/12 h-full relative z-0">
        <div id="app-info" class="w-full px-5 rounded-lg shadow-md absolute z-10" v-if="one">
          <div class="w-full px-5 pt-5">
            <img
              src="static/profile.jpg"
              alt="profile image"
              srcset
              class="rounded-full border-2 border-orange"
            />
          </div>
          <div class="w-full text-center">
            <h3 class="text-white text-2xl ubuntu">Alfredo Romero</h3>
            <h5 class="text-orange text-1xl ubuntu">Desarrollador Web</h5>
          </div>
          <div class="w-full text-center my-5">
            <ul class="flex justify-around">
              <li>
                <img src="static/github.svg" alt class="w-5 cursor-pointer" />
              </li>
              <li>
                <img src="static/linkedin.svg" alt class="w-5 cursor-pointer" />
              </li>
              <li>
                <img src="static/twitter.svg" alt class="w-5 cursor-pointer" />
              </li>
              <li>
                <img src="static/spotify.svg" alt class="w-5 cursor-pointer" />
              </li>
              <li>
                <img src="static/instagram.svg" alt class="w-5 cursor-pointer" />
              </li>
            </ul>
          </div>
          <hr />
          <div class="w-full mt-5 py-3 px-4">
            <button
              class="w-full mb-3 block mx-auto bg-white hover:bg-gray-100 text-gray-800 font-semibold ubuntu py-2 px-4 rounded shadow"
            >Descargar CV</button>
            <button
              class="w-full mb-3 block mx-auto bg-white hover:bg-gray-100 text-gray-800 font-semibold ubuntu py-2 px-4 rounded shadow"
            >Contactar</button>
          </div>
        </div>
      </div>
      <div class="w-8/12 h-full relative z-0 overflow-y-auto" id="router-view">
        <router-view></router-view>
      </div>
      <div class="w-1/12 relative z-0">
        <div id="app-menu" class="rounded-lg shadow-md absolute z-10">
          <ul>
            <li class="text-center py-3 cursor-pointer">
              <router-link to="/">
                <img src="static/user.svg" alt class="w-1/3 mx-auto" />
                <span>Sobre M√≠</span>
              </router-link>
            </li>
            <li class="text-center py-3 cursor-pointer">
              <router-link to="/resume">
                <img src="static/file.svg" alt class="w-1/3 mx-auto" />
                <span>Resumen</span>
              </router-link>
            </li>
            <!-- <li class="text-center py-3 cursor-pointer">
              <img src="static/work.svg" alt class="w-1/3 mx-auto" />
              <span>Trabajos</span>
            </li>
            <li class="text-center py-3 cursor-pointer">
              <img src="static/contact.svg" alt class="w-1/3 mx-auto" />
              <span>Contact</span>
            </li>-->
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      one: false,
    };
  },
  mounted() {
    this.one = true;
  },
  methods: {
    link: (path) => {
      this.$router.push({ path: path });
    },
  },
};
</script>

<style>
body {
  margin: 0;
  padding: 0;
  background-image: linear-gradient(120deg, #f6d365 0%, #fda085 100%);
  background-size: cover;
  background-repeat: no-repeat;
  overflow: hidden;
  font-size: 16px;
}
#app {
  height: 100vh;
  width: 100%;
  display: relative;
}

#app-info {
  background-color: #14213d;
  height: 105%;
  top: -2.5%;
  left: -2.5%;
  animation: scale-up-hor-center 1s cubic-bezier(0.22, 1, 0.36, 1) both;
  animation-delay: 1s;
}

#app-container {
  background-color: #ffffff;
  height: 70vh;
  margin-top: 15vh;
  animation: scale-in-hor-left 1s cubic-bezier(0.22, 1, 0.36, 1) both;
}

#app-menu {
  background-color: #ffffff;
  top: -2.5%;
  right: -20%;
  animation: slide-in-right 1s cubic-bezier(0.22, 1, 0.36, 1) both;
  animation-delay: 1.5s;
}

#app-menu > ul > li:not(:last-child) {
  border-bottom: 2px solid #14213d;
}

.text-orange {
  color: #fda384;
}
.border-orange {
  border-color: #fda384;
}

.custom-enter-active {
  animation: custom-in 0.5s;
}
.custom-leave-active {
  animation: custom-in 0.5s reverse;
}

.pill {
  margin: 0.2rem 0.5rem;
  padding: 0.2rem 0.5rem;
  border-radius: 26px;
  color: #ffffff;
  background-image: radial-gradient(
    circle 248px at center,
    #16d9e3 0%,
    #30c7ec 47%,
    #46aef7 100%
  );
}

/**
 * ----------------------------------------
 * animations
 * ----------------------------------------
 */
@-webkit-keyframes scale-up-hor-center {
  0% {
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
  }
  100% {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
  }
}
@keyframes scale-up-hor-center {
  0% {
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
  }
  100% {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
  }
}

@-webkit-keyframes slide-in-right {
  0% {
    -webkit-transform: translateX(1000px);
    transform: translateX(1000px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes slide-in-right {
  0% {
    -webkit-transform: translateX(1000px);
    transform: translateX(1000px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
    opacity: 1;
  }
}

@-webkit-keyframes scale-in-hor-left {
  0% {
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transform-origin: 0% 0%;
    transform-origin: 0% 0%;
    opacity: 1;
  }
  100% {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
    -webkit-transform-origin: 0% 0%;
    transform-origin: 0% 0%;
    opacity: 1;
  }
}
@keyframes scale-in-hor-left {
  0% {
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transform-origin: 0% 0%;
    transform-origin: 0% 0%;
    opacity: 1;
  }
  100% {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
    -webkit-transform-origin: 0% 0%;
    transform-origin: 0% 0%;
    opacity: 1;
  }
}

/**
 * ----------------------------------------
 * fonts
 * ----------------------------------------
 */
@import url("https://fonts.googleapis.com/css2?family=Poppins&family=Ubuntu:wght@400;500;700&display=swap");
.poppins {
  font-family: "Poppins", sans-serif;
}
.ubuntu {
  font-family: "Ubuntu", sans-serif;
}

#router-view::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
  background-color: #f5f5f5;
  border-radius: 10px;
}

#router-view::-webkit-scrollbar {
  width: 10px;
  background-color: #f5f5f5;
}

#router-view::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #fff;
  background-image: -webkit-linear-gradient(
    top,
    #e4f5fc 0%,
    #9da6ca 30%,
    #495e8b 60%,
    #274164 100%
  );
}

.h-100 {
  height: 100%;
}
</style>



