import Vue from 'vue'
import Router from 'vue-router'
import About from '@/components/About';
import Resume from '@/components/Resume';
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'About',
      component: About
    },{
      path: '/resume',
      name: 'Resume',
      component: Resume
    }
  ]
})



